<!--
 * @Author: Marlon
 * @Date: 2020-02-18 10:42:42
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-22 14:50:03
 * @Description: 默认主页
 -->
 <template>
  <el-row :gutter="12" class="card">
    <el-col :span="6" class="a">
      <el-card shadow="always">
        <div class="card-icon-lt card-1">
          <i class="el-icon-money card-icon"></i>
        </div>
        <div class="card-icon-rt">
          <div>
            <countTo :startVal="0" :endVal="164" :duration="3000" :decimals="0"></countTo>
          </div>
          <span>总项目数</span>
        </div>
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card shadow="always">
        <div class="card-icon-lt card-2">
          <i class="el-icon-coin card-icon"></i>
        </div>
        <div class="card-icon-rt">
          <div>
            <countTo :startVal="0" :endVal="164" :duration="3000" :decimals="0"></countTo>
          </div>
          <span>总数据库个数</span>
        </div>
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card shadow="always">
        <div class="card-icon-lt card-3">
          <i class="el-icon-bank-card card-icon"></i>
        </div>
        <div class="card-icon-rt">
          <div>
            <countTo :startVal="0" :endVal="164" :duration="3000" :decimals="0"></countTo>
          </div>
          <span>总数据表个数</span>
        </div>
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card shadow="always">
        <div class="card-icon-lt card-4">
          <i class="el-icon-share card-icon"></i>
        </div>
        <div class="card-icon-rt">
          <div>
            <countTo :startVal="0" :endVal="164" :duration="3000" :decimals="0"></countTo>
          </div>
          <span>总字段个数</span>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>
 
 <script>
import countTo from "vue-count-to";
import API from "api/index";
export default {
  components: {
    countTo
  },
  mounted() {
    // console.log(API);
  }
};
</script>
 
<style lang="scss" scoped>
.card {
  /deep/ .el-card__body {
    height: 150px;
    cursor: pointer;
    display: flex;
  }
  &-icon-lt {
    float: left;
    width: 120px;
    text-align: center;
  }
  &-icon-rt {
    flex: 1;
    float: left;
    text-align: center;
    & > div {
      height: 110px;
      font-size: 50px;
      color: #515a6e;
      &::before {
        display: inline-block;
        content: "";
        height: 100%;
        vertical-align: middle;
      }
    }
    & > span {
      font-size: 16px;
      color: #515a6e;
    }
  }
  &-icon {
    font-size: 50px;
    margin: 50px 0;
    color: #fff;
  }
  // 颜色
  .card-1 {
    background: rgb(45, 140, 240);
  }
  .card-2 {
    background: rgb(25, 190, 107);
  }
  .card-3 {
    background: rgb(255, 153, 0);
  }
  .card-4 {
    background: rgb(154, 102, 228);
  }
}
</style>


